.header {
  &__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0 20px;
    border-bottom: 1px solid $gray-border;
    @media (max-width: 600px) {
      border: none;
    }
  }
  &__controls {
    display: flex;
    flex-basis: 450px;
    @media (max-width: 991px) {
      justify-content: space-between;
      flex-basis: 20%;
    }
    @media (max-width: 600px) {
      flex-basis: 30%;
    }
    &--end {
      justify-content: flex-end;
      @media (max-width: 991px) {
        justify-content: space-between;
      }
    }
  }
  &__control {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 1;
    position: relative;
    cursor: pointer;
    @media (max-width: 991px) {
      margin: 0 5px;
    }
    &:hover {
      .header__control-icon {
        background-color: $secondary;
        transition: 0.2s ease-in-out;
        @media (max-width: 991px) {
          background-color: transparent;
        }
        .icon {
          filter: invert(1) brightness(2);
          transition: 0.2s ease-in-out;
          @media (max-width: 991px) {
            filter: none;
          }
        }
      }
    }
    span {
      @media (max-width: 991px) {
        display: none;
      }
    }
  }
  &__shops {
    text-decoration-color: $primary;
    &:hover {
      text-decoration-color: transparent;
    }
  }
  &__search {
    margin-right: 45px;
    cursor: pointer;
    @media (max-width: 991px) {
      margin-right: 5px;
    }
    &:hover {
      text-decoration-color: $primary;
    }
    span {
      &.hidden {
        display: none;
      }
    }
  }
  &__search-input {
    width: 210px;
    border: none;
    border-bottom: 1px solid $gray-border;
    padding: 3px 0 2px;
    display: none;
    &.visible {
      display: block;
    }
    &::placeholder {
      font-size: 14px;
      color: $primary;
    }
  }
  &__login {
    margin-right: 33px;
    text-decoration-color: $primary;
    @media (max-width: 991px) {
      margin-right: 5px;
    }
    &:hover {
      text-decoration-color: transparent;
    }
  }
  &__favourite {
    margin-right: 36px;
    @media (max-width: 991px) {
      margin-right: 5px;
    }
    .header__control-icon {
      margin-right: 0;
    }
  }
  &__cart {
    .header__control-icon {
      margin-right: 0;
    }
    .header__control-icon .icon {
      fill: $primary;
    }
  }
  &__mob-menu {
    width: 42px;
    display: none;
    justify-content: center;
    .icon {
      width: 19px;
      height: 17px;
    }
    @media (max-width: 991px) {
      display: flex;
    }
    @media (max-width: 600px) {
      width: auto;
    }
  }
  &__control-icon {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background-color: $beige;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    transition: 0.2s ease-in-out;
    @media (max-width: 991px) {
      margin-right: 0;
      background-color: transparent;
    }
    @media (max-width: 600px) {
      width: auto;
      height: auto;
    }
    .icon {
      width: 24px;
      height: 24px;
      fill: transparent;
      transition: 0.2s ease-in-out;
      @media (max-width: 600px) {
        width: 20px;
        height: 20px;
      }
    }
  }
  &__control-num {
    position: absolute;
    top: -5px;
    right: -5px;
    font-size: 12px;
    line-height: 1;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #333;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $white;
    @media (max-width: 600px) {
      width: 14px;
      height: 14px;
      font-size: 9px;
    }
  }
  &__logo {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      width: 153px;
      height: 105px;
      @media (max-width: 600px) {
        width: 85px;
        height: 66px;
      }
    }
  }
  &__logo-descriptor {
    font-family: Georgia, serif;
    font-size: 7px;
    line-height: 1;
    text-transform: uppercase;
    margin-top: 7px;
    letter-spacing: 0.4px;
    @media (max-width: 600px) {
      font-size: 4px;
      letter-spacing: initial;
      margin-top: 3px;
    }
  }
  &__bottom {
    position: relative;
    @media (max-width: 991px) {
      display: none;
    }
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__nav-item {
