    }
    &.active {
      &:before {
        opacity: 1;
        transition: 0.2s ease-in-out;
      }
    }
  }
  &__dropdown {
    position: absolute;
    width: 100%;
    z-index: 2;
    opacity: 0;
    transform: translateY(-10px);
    background-color: $white;
    .container {
      position: absolute;
      left: -9999px;
    }
    &.show {
      z-index: 3;
      opacity: 1;
      transform: translateY(0);
      transition: 0.3s ease-in-out;
      left: 0;
      .container {
        position: static;
      }
    }
  }
  &__dropdown-inner {
    display: flex;
    justify-content: space-between;
    padding: 45px 40px 65px 0;
    border-top: 1px solid $gray-border;
  }
  &__dropdown-subtitle {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 30px;
    color: $black;
    font-family: Georgia, serif;
  }
  &__dropdown-items {
    display: flex;
    flex-direction: column;
  }
  &__dropdown-item {
    font-size: 15px;
    line-height: 1;
    color: #7d7d7d;
    margin-bottom: 18px;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 20px;
      height: 1px;
      background-color: $secondary;
      left: -30px;
      top: 7px;
      opacity: 0;
      transition: 0.3s ease-in-out;
    }
    &:hover {
      transform: translateX(20px);
      color: $black;
      &:before {
        opacity: 1;
        transition: 0.3s ease-in-out;
      }
    }
  }
  .easy-autocomplete {
    z-index: 4;
  }
  .easy-autocomplete-container {
    box-shadow: 0 0 10px #eee;
    border-radius: 5px;
    width: 355px;
    background-color: $white;
  }
  .easy-autocomplete-container ul {
    margin: 25px 20px;
    border: none;
    max-height: 135px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-track {
      background: #E6E6E6;
    }

    &::-webkit-scrollbar-thumb {
      background: #DDCCA6;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #DDCCA6;
    }
  }
  .easy-autocomplete-container ul li, .easy-autocomplete-container ul .eac-category {
    border: none;
    padding: 4px 0;
    &:hover {
      background-color: #f9f6f0;
    }
  }
}
